*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a { 
    text-decoration: none;
}

html {
    scroll-behavior: smooth;
} 

body {
    font-family: 'Montserrat', sans-serif;
}

.container {
    width: 100%;
    max-width: 1520px;
    margin: 0 auto;
    padding: 0 1rem;
}

.top-block__title {
    font-weight: 800;
    color: #373737;
    font-size: 72px;
    text-transform: uppercase;
}

.top-block__title span {
    color: #0085FF;
}

.top-block__desc {
    font-weight: 500;
    font-size: 24px;
    color: #797979;
    display: inline-block;
    max-width: 633px;
    margin-top: 20px;
}

.top-block__inner {
    padding: 180px 0;
}

.top-block__btn {
    padding: 29px 47px;
    border-radius: 26px;
    background-color: #0085FF;
    display: inline-block;
    font-size: 18px;
    font-weight: 800;
    color: white;
    text-transform: uppercase;
    margin-top: 100px;
}

.top-block__portfolio-link {
    text-align: center;
    margin-top: 150px;
}

.top-block__portfolio-link svg {
    animation-name: scrollicon;
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;
    animation-duration: 0.7s;
    transition: transform .5s;
}

.top-block__portfolio-link a {
    font-weight: 500;
    color: #0085FF;
    font-size: 18px;
}

.skills__item {
    display: inline-block;
    text-align: center;
    font-weight: 500;
    font-size: 24px;
    padding: 0 80px;
    position: relative;
}

.skills__item-name {
    font-size: 64px;
    text-transform: lowercase;
    font-weight: 700;
}

.skills__inner {
    padding: 180px 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}
.skills__item::after {
    content: "";
    width: 1px;
    height: 162px;
    background-color: #0085FF;
    display: inline-block;
    position: absolute;
    top: 0;
    right: 0;
}

.skills__item:last-child::after {
    display: none;
}

.portfolio__inner {
    padding: 180px 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 60px 0;
}

.portfolio__item {
    width: 700px;
    height: 300px;
    overflow: hidden;
    border-radius: 52px;
    transition: filter .5s;
    position: relative;
}

.portfolio__item:hover {
    filter: drop-shadow(0 4px 50px #0085FF);
}

.portfolio__item:hover .portfolio__item-text {
    opacity: .8;
}

.portfolio__item-text {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    padding: 77px 89px;
    background-color: #0085FF;
    opacity: 0;
    transition: opacity .5s;
}

.project__title {
    font-size: 36px;
    color: rgba(255, 255, 255, 1);
    font-weight: 800;
}

.project__desc {
    color: rgba(238, 238, 238, 1);
    font-size: 18px;
    font-weight: 400;
    display: inline-block;
    margin-top: 20px;
}

.see-more .project__title {
    color: #373737;
}

.see-more .project__desc {
    color: black;
}

.see-more {
    width: 700px;
    height: 300px;
    filter: drop-shadow(0 4px 20px rgba(0, 0, 0, 0.25));
    border-radius: 52px;
    background-color: rgba(255, 255, 255, 1);
}

.see-more__txt {
    padding: 77px 89px;
}

.contact-me__title {
    font-weight: 800;
    font-size: 46px;
    text-transform: uppercase;
}

.contact-me__title span {
    color: #0085FF;
}

.contact-me__inner {
    padding: 180px 0;
}

.contact-me__items {
    margin-top: 60px;
    display: grid;
    grid-template-columns: repeat(2, min-content);
    gap: 20px 35px;
    align-items: center;
}

.contact-me__items span {
    font-size: 24px;
}

.contact-me {
    position: relative;
}

.top-block__inner * {
    position: relative;
    z-index: 1;
}


.top-block__decor, .contact-me__decor {
    position: absolute;
    top: 0;
    right: 0;
    width: calc(50vw + 14px);
    z-index: 0;
}

.top-block__portfolio-link a{
    position: sticky;
    top: 0;
}


@keyframes scrollicon {
    from {
        transform: translateY(0);
    }
    to {
        transform: translateY(5px);
    }
}

@media screen and (max-width: 900px) {
    .skills__inner {
        grid-template-columns: 1fr;
        gap: 60px 0;
    }
    .skills__item {
        padding: 0;
        padding-bottom: 60px;
    }
    .portfolio__item {
        justify-self: center;
        width: 80vw;
    }
    .see-more {
        justify-self: center;
        width: 80vw;
    }
    .skills__item:last-child {
        padding-bottom: 0;
    }
    .skills__item::after {
        width: 50%;
        height: 1px;
        top: initial;
        bottom: 0;
        right: 25%;
    }
    .portfolio__inner {
        grid-template-columns: 1fr;
    }
    .project__title {
        font-size: 24px;
    }
    .project__desc {
        font-size: 14px;
    }
    .contact-me__decor, .top-block__decor{
        display: none;
    }
    .top-block__title {
        font-size: 48px;
    }
    .contact-me__items span {
        font-size: 14px;
    }
    .contact-me__title {
        font-size: 36px;
    }
    .top-block__desc {
        font-size: 18px;
        max-width: unset;
    }
    .skills__item-name {
        font-size: 36px;
    }
    .skills__item {
        font-size: 14px;
    }
    .portfolio__item-text, .see-more__txt {
        padding: 77px 50px;
    }
    .top-block__portfolio-link a {
        font-size: 12px;
    }
}